import React from 'react'
import { Link, Route, Router, Switch } from 'react-router-dom'
import { createBrowserHistory } from 'history'
import { Home } from '../Home'
import { SignIn } from '../SignIn'
import { SignUp } from '../SignUp'
import './Cookbook.css'

const Cookbook = () => {
  return (
    <Router history={createBrowserHistory()}>
      <div className='containerNav'>
        <nav className='nav'>
          <Link to='/cookbook' className='cookbook item'>Cookbook</Link>
          <Link to='/cookbook/sign-up' className='position signup'>Sign Up</Link>
          <Link to='/cookbook/sign-in' className='item position signin'>Sign In</Link>
        </nav>
      </div>
      
      <Switch>
        <Route exact path='/cookbook'  component={ Home } />
        <Route path='/cookbook/sign-in' component={ SignIn } />
        <Route path='/cookbook/sign-up' component={ SignUp } />
      </Switch>
    </Router>
  )
}

export { Cookbook }
